.settings {
    font-family: "Avenir";
}

#addAddressModal,
#editAddressModal {
    font-family: "Avenir";
    color: #FBFBEF;
}

.modal-header,
.modal-body,
.modal-footer {
    background: #312C3B;
}

.basic-user-info-container {
    margin-top: 40px;
}

.basic-user-info-container .input-box {
    background: #FBFBEF;
    border-radius: 20px;
    padding-left: 15px;
    padding-top: 8px;
    padding-bottom: 8px;
    border: none;
    width: 80%;
    padding-right: 20px;
    margin-bottom: 20px;
    margin-left: 8px;
}

