div.card.h-100{
    height: 200px !important;
}

.card-body {
    background-color: #FBFBEF;
}

.card-text-bold {
    font-weight: 700;
    margin-bottom: 10px;
}

.profile-picture {
    border-radius: 50%;
}

.heading1 {
    font-family: 'Scope One', serif;
    color: #FBFBEF;
}

.heading2 {
    font-family: 'Scope One', serif;
    color: #FBFBEF;
    text-align: left;
}

.form-label {
    float: left;
}

.settings a {
    color: #312C3B;
}

.settings .heading1 {
    text-align: left;
}

.flex-box-save-button {
    display:flex;
    justify-content: flex-end;
    width: 84%;
}

div#edit-information-div-btn.row.flex-box-save-button{
    margin-left: 15% !important;
}

button#edit-information-save-btn.btn.save-button, .submit-button{
    display: flex;
    flex-direction: row;
    padding: 12px 18px;
    background: #6AA17B;
    border-color: transparent;
    border: 2px solid transparent;
    border-radius: 50px !important;
    color: #26222E;
    margin-top: 10px;
    font-family: "Avenir";
    font-size: 20px;
    font-weight: 500;
    line-height: 16px;
}

button#edit-information-save-btn.btn.save-button:hover, .submit-button:hover {
    color: #6AA17B;
    background: transparent;
    border-color: #6AA17B;
}

.input-box-modal {
    background: #FBFBEF;
    border-radius: 20px;
    border: none;
}

.card-link {
    text-decoration: none;
}

.card-link:hover {
    color: #6AA17B;
}

.addresses-container {
    padding-bottom: 50px;
}

.flex-box-submit-button {
    display:flex;
    justify-content: flex-end;
}

.settings i {
    color: #26222E;
    margin: 0px;
    margin-right: 4px;
}

.settings a:hover {
    color: #6AA17B;
}

.settings .card-text {
    margin-bottom: 10px;
}
.container.address-card {
    display: inline;
    padding-right: 8px;
}

.edit-address-block {
    margin-top: 32px;
}

.container.edit-card {
    text-align: center;
}

.edit-link {
    padding-right: 8px;
}
